"use client";
import React, { useState } from "react";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "../../../../components/ui/table";
import Badge from "../../../../components/ui/badge/Badge";
import { Modal } from "../../../../components/ui/modal";
import Image from "next/image";
import jsPDF from "jspdf";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";

interface Payslip {
    id: number;
    payPeriod: string;
    grossPay: number;
    deductions: number;
    netPay: number;
    status: "Paid" | "Pending";
    employeeName: string;
    employeeId: string;
    dateOfJoining: string;
    location: string;
    payableDays: number;
    lopDays: number;
    pan: string;
    uan: string;
    pfNumber: string;
    esicNumber: string;
    bankName: string;
    accountNumber: string;
    earnings: { label: string; amount: number }[];
    deductionsDetails: { label: string; amount: number }[];
}

const payslipsData: Payslip[] = [
    {
        id: 1,
        payPeriod: "October 2025",
        grossPay: 6500,
        deductions: 1430,
        netPay: 5070,
        status: "Paid",
        employeeName: "John Doe",
        employeeId: "EMP-2024-001",
        dateOfJoining: "01-Jan-2024",
        location: "New York",
        payableDays: 30,
        lopDays: 0,
        pan: "ABCDE1234F",
        uan: "123456789012",
        pfNumber: "PF12345678",
        esicNumber: "ESIC1234567",
        bankName: "Bank of America",
        accountNumber: "1234567890",
        earnings: [
            { label: "Base Salary", amount: 6000 },
            { label: "Performance Bonus", amount: 500 },
        ],
        deductionsDetails: [
            { label: "Federal Tax", amount: 980 },
            { label: "Social Security", amount: 403 },
            { label: "Health Insurance", amount: 47 },
        ],
    },
];

export default function PayslipsPage() {
    const [selectedPayslip, setSelectedPayslip] = useState<Payslip | null>(null);

    const handleView = (payslip: Payslip) => setSelectedPayslip(payslip);
    const handleClose = () => setSelectedPayslip(null);

    const handleDownloadPDF = () => {
        if (!selectedPayslip) return;

        const doc = new jsPDF();
        doc.setFontSize(16);
        doc.text("Payslip", 105, 15, { align: "center" });
        doc.setFontSize(12);

        let y = 25;
        doc.text(`Employee Name: ${selectedPayslip.employeeName}`, 20, y);
        y += 7;
        doc.text(`Employee ID: ${selectedPayslip.employeeId}`, 20, y);
        y += 7;
        doc.text(`Date of Joining: ${selectedPayslip.dateOfJoining}`, 20, y);
        y += 7;
        doc.text(`Location: ${selectedPayslip.location}`, 20, y);
        y += 7;
        doc.text(`Payable Days: ${selectedPayslip.payableDays}`, 20, y);
        y += 7;
        doc.text(`LOP Days: ${selectedPayslip.lopDays}`, 20, y);
        y += 7;
        doc.text(`PAN: ${selectedPayslip.pan}`, 20, y);
        y += 7;
        doc.text(`UAN: ${selectedPayslip.uan}`, 20, y);
        y += 7;
        doc.text(`PF Number: ${selectedPayslip.pfNumber}`, 20, y);
        y += 7;
        doc.text(`ESIC Number: ${selectedPayslip.esicNumber}`, 20, y);

        y += 10;
        doc.text(`Bank Name: ${selectedPayslip.bankName}`, 20, y);
        y += 7;
        doc.text(`Account Number: ${selectedPayslip.accountNumber}`, 20, y);

        y += 10;
        doc.text("Earnings", 20, y);
        selectedPayslip.earnings.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 25, y);
        });

        y += 10;
        doc.text("Deductions", 20, y);
        selectedPayslip.deductionsDetails.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 25, y);
        });

        y += 7;
        doc.text(`Net Pay: $${selectedPayslip.netPay.toFixed(2)}`, 20, y);

        y += 15;
        doc.text(
            "This payslip is computer generated and does not require any signature.",
            20,
            y
        );

        doc.save(`Payslip_${selectedPayslip.payPeriod}.pdf`);
    };

    return (
        <div>
            {/* Page Breadcrumb */}
            <PageBreadcrumb pageTitle="Payslips" />

            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-white/[0.05] dark:bg-white/[0.03] p-6">
                <h1 className="text-2xl font-bold mb-6">Payslips</h1>

                <div className="max-w-full overflow-x-auto">
                    <div className="min-w-[1102px]">
                        <Table>
                            <TableHeader className="border-b border-gray-100 dark:border-white/[0.05]">
                                <TableRow>
                                    <TableCell isHeader>Pay Period</TableCell>
                                    <TableCell isHeader>Gross Pay</TableCell>
                                    <TableCell isHeader>Deductions</TableCell>
                                    <TableCell isHeader>Net Pay</TableCell>
                                    <TableCell isHeader>Status</TableCell>
                                    <TableCell isHeader>Action</TableCell>
                                </TableRow>
                            </TableHeader>
                            <TableBody className="divide-y divide-gray-100 dark:divide-white/[0.05]">
                                {payslipsData.map((p) => (
                                    <TableRow key={p.id}>
                                        <TableCell>{p.payPeriod}</TableCell>
                                        <TableCell>${p.grossPay.toFixed(2)}</TableCell>
                                        <TableCell>${p.deductions.toFixed(2)}</TableCell>
                                        <TableCell>${p.netPay.toFixed(2)}</TableCell>
                                        <TableCell>
                                            <Badge size="sm" color={p.status === "Paid" ? "success" : "warning"}>
                                                {p.status}
                                            </Badge>
                                        </TableCell>
                                        <TableCell>
                                            <button
                                                onClick={() => handleView(p)}
                                                className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
                                            >
                                                View
                                            </button>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </div>

                {/* Payslip Modal */}
                <Modal isOpen={!!selectedPayslip} onClose={handleClose} className="max-w-3xl p-6">
                    {selectedPayslip && (
                        <div className="space-y-6">
                            <div className="flex justify-center">
                                <Image
                                    src="/images/company-logo.png"
                                    alt="Company Logo"
                                    width={120}
                                    height={60}
                                />
                            </div>

                            <h2 className="text-xl font-bold text-center">Payslip</h2>

                            {/* Employee Details */}
                            <div className="grid grid-cols-2 gap-4 border p-4 rounded-lg bg-gray-50 dark:bg-gray-800">
                                <div>
                                    <p><strong>Employee Name:</strong> {selectedPayslip.employeeName}</p>
                                    <p><strong>Employee ID:</strong> {selectedPayslip.employeeId}</p>
                                    <p><strong>Date of Joining:</strong> {selectedPayslip.dateOfJoining}</p>
                                    <p><strong>Location:</strong> {selectedPayslip.location}</p>
                                    <p><strong>Payable Days:</strong> {selectedPayslip.payableDays}</p>
                                </div>
                                <div>
                                    <p><strong>LOP Days:</strong> {selectedPayslip.lopDays}</p>
                                    <p><strong>PAN:</strong> {selectedPayslip.pan}</p>
                                    <p><strong>UAN:</strong> {selectedPayslip.uan}</p>
                                    <p><strong>PF Number:</strong> {selectedPayslip.pfNumber}</p>
                                    <p><strong>ESIC Number:</strong> {selectedPayslip.esicNumber}</p>
                                </div>
                            </div>

                            {/* Bank Details */}
                            <div className="border p-4 rounded-lg bg-gray-50 dark:bg-gray-800">
                                <p><strong>Bank Name:</strong> {selectedPayslip.bankName}</p>
                                <p><strong>Account Number:</strong> {selectedPayslip.accountNumber}</p>
                            </div>

                            {/* Earnings & Deductions */}
                            <div className="grid grid-cols-2 gap-4">
                                <div className="border p-4 rounded-lg bg-gray-50 dark:bg-gray-800">
                                    <h3 className="font-semibold mb-2">Earnings</h3>
                                    <ul>
                                        {selectedPayslip.earnings.map((e) => (
                                            <li key={e.label}>{e.label}: ${e.amount.toFixed(2)}</li>
                                        ))}
                                    </ul>
                                    <p className="font-bold mt-2">Gross Pay: ${selectedPayslip.grossPay.toFixed(2)}</p>
                                </div>
                                <div className="border p-4 rounded-lg bg-gray-50 dark:bg-gray-800">
                                    <h3 className="font-semibold mb-2">Deductions</h3>
                                    <ul>
                                        {selectedPayslip.deductionsDetails.map((d) => (
                                            <li key={d.label}>{d.label}: ${d.amount.toFixed(2)}</li>
                                        ))}
                                    </ul>
                                    <p className="font-bold mt-2">Total Deductions: ${selectedPayslip.deductions.toFixed(2)}</p>
                                </div>
                            </div>

                            {/* Net Pay */}
                            <div className="text-center text-lg font-bold mt-4">
                                Net Pay: ${selectedPayslip.netPay.toFixed(2)}
                            </div>

                            {/* Footer */}
                            <div className="text-center text-sm text-gray-500 mt-4">
                                This payslip is computer generated and does not require any signature
                            </div>

                            <div className="flex justify-end mt-4">
                                <button
                                    onClick={handleDownloadPDF}
                                    className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                                >
                                    Download PDF
                                </button>
                            </div>
                        </div>
                    )}
                </Modal>
            </div>
        </div>
    );
}
